@import "../assets/style/variable.scss";

.about {
  margin: 0 auto;
  background-color: $bodyBlack;
  .intoduce {
    margin: 0 60px;
    border-top: 1px solid #fff;
    display: flex;
    margin-bottom: 10vh;
    img {
      width: 240px;
      height: 320px;
      border: 1px solid #fff;
    }
    .txt {
      padding: 30px 0 0 50px;
      p {
        color: #ddd;
        margin-bottom: 20px;
        font-size: 26px;
        font-weight: 600;
        word-break: keep-all;
      }
      .tag {
        color: #efefef;
        span {
          font-size: 1.2em;
          margin-right: 20px;
        }
      }

      .connect {
        margin-top: 20px;
        border: none;
        width: 150px;
        height: 40px;
        border-radius: 30px;
        font-size: 1em;
        font-family: "Pretendard";
        font-weight: 600;
        text-transform: capitalize;
        background-color: rgb(145, 145, 145);
        // filter: opacity(0.3);
        color: #222;
        transition: 0.4s;
        &:hover {
          filter: none;
          // border: 1px solid #fff;
          cursor: pointer;
          background-color: #222;
          color: #fff;
          box-shadow: rgba(235, 235, 235, 0.35) 0px 1px 10px;
        }
      }
    }
  }
  .skill {
    margin-bottom: 10vh;
    h2 {
      margin: 0 auto;
    }
    section {
      display: flex;
      padding: 30px;
      h4 {
        svg {
          width: 70px;
        }
      }
      ul {
        padding: 1vw;
        li {
        }
      }
    }
  }
  .contact {
    h4,
    h5 {
      font-size: 60px;
      font-weight: 700;
      text-transform: uppercase;
      line-height: 1.1;
      font-family: "DM Serif Display", serif;
      color: $colorLightGreen;
    }
    h5 {
      text-transform: none;
    }
  }
}
